body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.navbar {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  background-color: transparent;
  padding: 20px 140px;
  /* 상하 20px, 좌우 140px */
  color: black;
  border-bottom: 1px solid black;
}

.container {
  padding: 20px 140px;
  /* 상하 20px, 좌우 140px */
}

.question-list {
  list-style: none;
  padding: 0;
}

.question-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5px 0;
}

.delete-btn {
  padding: 5px 10px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.delete-btn:hover {
  background-color: darkred;
}

.answer-box {
  margin: 10px 0;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: #f9f9f9;
}

.edit-btn {
  margin-top: 10px;
  padding: 5px 10px;
  background-color: blue;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.edit-btn:hover {
  background-color: darkblue;
}


.answer-box textarea {
  width: 100%;
  padding: 8px;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 16px;
  resize: vertical;
  /* 크기 조정을 제한하려면 사용 */
  overflow-y: auto;
  /* 내용이 길어질 경우 스크롤 표시 */
}

.edit-textarea {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  resize: none;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  width: 90%;
  display: flex;
  /* 정렬을 위해 flex 사용 */
  flex-direction: column;
  /* 세로 방향 정렬 */
  justify-content: center;
  /* 세로 가운데 정렬 */
  align-items: center;
  /* 가로 가운데 정렬 */
  text-align: center;
}

.modal-content form {
  width: 90%;
  text-align: left;
}

.modal-content input {
  display: block;
  width: 100%;
  height: 100px;
  margin: 5px 0;
  /* 위아래 간격 설정 */
  padding: 10px;
  /* 내부 여백 설정 */
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  /* 패딩 포함 크기 계산 */
}

.modal-buttons {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  /* 버튼 간 간격 */
  margin-top: 20px;
  /* 버튼 위 간격 */
  width: 20%;
}

.modal-buttons button {
  flex: 1;
  /* 버튼 크기 균등 */
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
  margin: 0 5px;
  /* 버튼 간 추가 간격 */
}

button:hover {
  background-color: #0056b3;
}